/* get components
 * show CSS components
 */

import cardImage from '../image/components/card-image.png';
import menuImageMagic from '../image/components/hat.svg';
import menuImageHTML from '../image/components/html.svg';
import menuImageCSS from '../image/components/css.svg';
import menuImageJS from '../image/components/javascript.svg';
import ApplicationError from './Error';

let timeOut;

function searchComponents() {
    try {
        const input = this;
        const value = input.value;
        clearTimeout(timeOut);
        timeOut = setTimeout(() => {
            input.disabled = true;
            input.classList.add('is-disabled');
            loading();
            setTimeout(() => {
                input.disabled = false;
                input.classList.remove('is-disabled');
            }, 3000);
            setTimeout(() => {
                const components = document.querySelector('.components');
                components.classList.remove(...components.classList);
                components.classList.add('components');
                getComponents(value, input);
            }, 1000);
        }, 2700);
    } catch (err) {
        const error = new ApplicationError('get components');
        error.showMessage();
        throw new Error(err);
    }
}

function getComponents(value) {
    try {
        switch (value.toLowerCase()) {
            case 'preloader':
                getPreloader();
                break;
            case 'button':
                getButton();
                break;
            case 'card':
                getCard();
                break;
            case 'menu':
                getMenu();
                break;
            case 'table':
                getTable();
                break;
            case 'form':
                getForm();
                break;
            case 'slider':
                getSlider();
                break;
            case 'accordion':
                getAccordion();
                break;
            default:
                getDefault();
                break;
        }
    } catch (err) {
        const error = new ApplicationError('get components');
        error.showMessage();
        throw new Error(err);
    }
}

function getAccordion() {
    try {
        const components = document.querySelector('.components');
        components.innerHTML = '';
        components.insertAdjacentHTML(
            'beforeend',
            `
        <div class="accordion">
            <div class="accordion__box box">
                <h2 class="box__heading">Accordion</h2>
                <p class="box__content">
                    The accordion function is provided by the flex-basis property
                </p>
                <div class="box__footer">
                    <div class="box__tag box__tag--first">HTML</div>
                    <div class="box__tag">CSS</div>
                    <div class="box__tag box__tag--last">JavaScript</div>
                </div>
            </div>
            <div class="accordion__box box">
                <h2 class="box__heading">MAGICSS</h2>
                <p class="box__content">
                    The color of the components is changed using currentColor
                </p>
                <div class="box__footer">
                    <div class="box__tag box__tag--first">HTML</div>
                    <div class="box__tag">CSS</div>
                    <div class="box__tag box__tag--last">JavaScript</div>
                </div>
            </div>
            <div class="accordion__box box">
                <h2 class="box__heading">CSS is fun</h2>
                <p class="box__content">
                    Automatically generated meaningless and useless text... absolutely
                </p>
                <div class="box__footer">
                    <div class="box__tag box__tag--first">HTML</div>
                    <div class="box__tag">CSS</div>
                    <div class="box__tag box__tag--last">JavaScript</div>
                </div>
            </div>
        </div>
        `,
        );
        const accordion = document.querySelector('.accordion');
        changeTheme(accordion, components);
    } catch (err) {
        const error = new ApplicationError('accordion');
        error.showMessage();
        throw new Error(err);
    }
}

function getSlider() {
    try {
        const components = document.querySelector('.components');
        components.innerHTML = '';
        components.insertAdjacentHTML(
            'beforeend',
            `
        <div class="slider">
            <div class="slider__slide slider__slide--1">
                <h1 class="slide__heading slider__heading--bold">MAGICSS</h1>
            </div>
            <div class="slider__slide slider__slide--2">
                <h2 class="slider__heading">CSS slider</h2>
                <p class="slider__content">
                    This slider only uses CSS.
                </p>
            </div>
            <div class="slider__slide slider__slide--3">
                <h2 class="slider__heading">Heading of slide</h2>
                <p class="slider__content">
                    There might have been a picture here, but we lost it...
                </p>
            </div>
        </div>
        `,
        );
        const slider = document.querySelector('.slider');
        changeTheme(slider, components);
    } catch (err) {
        const error = new ApplicationError('slider');
        error.showMessage();
        throw new Error(err);
    }
}

