/* button by start page
 * get button and add event
 */

const main = document.querySelector('.layout-main');
const components = document.querySelector('[data-navigation="components"]');

export default function getButton() {
    let button = document.querySelector('.main-button');
    if (!button) {
        button = document.createElement('button');
        button.classList.add('main-button', 'is-hide');
        button.textContent = 'get css components';
    }
    button.addEventListener('click', () => {
        button.classList.add('is-hide');
        setTimeout(() => components.click(), 500);
    });
    main.append(button);
    setTimeout(() => button.classList.remove('is-hide'), 500);
}

getButton();
