/* burger menu
 * display settings burger icon and interactions with it
 */

import ApplicationError from './Error';

const navigation = document.querySelector('.navigation');
const textLogo = document.querySelector('.text-logo');
const header = document.querySelector('.layout-header');

const burgerMenu = document.createElement('div');
const burger = document.createElement('div');
const burgerNavigation = document.createElement('div');

function getBurger() {
    try {
        burgerMenu.classList.add('burger-menu');
        burgerNavigation.classList.add('burger-navigation');
        burger.classList.add('burger');
        for (let i = 0; i < 3; i++) {
            const burgerLine = document.createElement('div');
            burgerLine.classList.add('burger-line');
            burger.append(burgerLine);
        }

        burgerMenu.append(burger, burgerNavigation);
    } catch (err) {
        burger.removeEventListener('click', click);
        window.removeEventListener('DOMContentLoaded', size);
        window.removeEventListener('resize', size);
        const error = new ApplicationError('burger menu');
        error.showMessage();
        getDesktopNavigation();
        throw new Error(err);
    }
}

function click() {
    try {
        burgerNavigation.classList.toggle('is-width-100vw');
        const bool = burger.getAttribute('data-burger');
        const { children } = burger;

        if (!bool) {
            burger.setAttribute('data-burger', 'click');
            for (let i = 0; i < children.length; i++) {
                if (i === 0) children[i].classList.add('is-line-top');
                if (i === 1) children[i].classList.add('is-line-none');
                if (i === 2) children[i].classList.add('is-line-bottom');
            }
        }

        if (bool) {
            burger.removeAttribute('data-burger');
            for (let i = 0; i < children.length; i++) {
                children[i].classList.remove(...children[i].classList);
                children[i].classList.add('burger-line');
            }
        }
    } catch (err) {
        burger.removeEventListener('click', click);
        window.removeEventListener('DOMContentLoaded', size);
        window.removeEventListener('resize', size);
        const error = new ApplicationError('burger menu');
        error.showMessage();
        getDesktopNavigation();
        throw new Error(err);
    }
}

function size() {
    try {
        if (this.innerWidth <= 620) {
            getMobileNavigation();
        } else {
            getDesktopNavigation();
        }
    } catch (err) {
        window.removeEventListener('DOMContentLoaded', size);
        window.removeEventListener('resize', size);
        const error = new ApplicationError('adoptable menu');
        error.showMessage();
        throw new Error(err);
    }
}

function getMobileNavigation() {
    try {
        header.removeAttribute('data-desktop');
        const isMobile = header.getAttribute('data-mobile');
        if (isMobile) return;

        header.append(burgerMenu);

        burgerNavigation.append(navigation);
        header.append(textLogo);

        header.setAttribute('data-mobile', 'true');
    } catch (err) {
        burger.removeEventListener('click', click);
        window.removeEventListener('DOMContentLoaded', size);
        window.removeEventListener('resize', size);
        const error = new ApplicationError('navigation');
        error.showMessage();
        getDesktopNavigation();
        throw new Error(err);
    }
}

function getDesktopNavigation() {
    try {
        header.removeAttribute('data-mobile');
        const isDesktop = header.getAttribute('data-desktop');
        if (isDesktop) return;

        burger.removeAttribute('data-burger');
        document.querySelectorAll('.burger-line').forEach((element) => {
            element.classList.remove(...element.classList);
            element.classList.add('burger-line');
        });

        burgerMenu.remove();
        header.append(navigation, textLogo);
        burgerNavigation.classList.remove('is-width-100vw');

        header.setAttribute('data-desktop', 'true');
    } catch (err) {
        window.removeEventListener('DOMContentLoaded', size);
        window.removeEventListener('resize', size);
        const error = new ApplicationError('navigation');
        error.showMessage();
        getMobileNavigation();
        throw new Error(err);
    }
}

getBurger();

window.addEventListener('DOMContentLoaded', size);
window.addEventListener('resize', size);
burger.addEventListener('click', click);

export { burger, burgerNavigation };
