/* change color for navigation items
 * mouseover and mouseout
 */

const classColors = [
    'is-dark',
    'is-cyan',
    'is-blue',
    'is-pink',
    'is-red',
    'is-purple',
    'is-lime',
    'is-brown',
    'is-orange',
];

let currentColor;
const navigation = document.querySelector('.navigation');

function over(event) {
    try {
        const { target } = event;
        const bool = target.classList.contains('navigation-list-item');

        if (bool) {
            const randomNumber = Math.trunc(Math.random() * 9);
            const giveColor = classColors[randomNumber];

            if (currentColor === giveColor) {
                over.call(this, event);
                return;
            }
            currentColor = giveColor;

            target.classList.add(giveColor);
        }
    } catch (err) {
        const { target } = event;
        target.classList.add('is-blue');
        throw new Error(err);
    }
}

function out(event) {
    try {
        const { target } = event;
        const bool = target.classList.contains('navigation-list-item');

        if (bool) {
            const deleteClass = classColors
                .filter((_class) => target.classList.contains(_class))
                .toString();
            target.classList.remove(deleteClass);
        }
    } catch (err) {
        throw new Error(err);
    }
}

navigation.addEventListener('mouseover', over);
navigation.addEventListener('mouseout', out);
