/* Creating Error for application
 * export class Error
 */

export default class ApplicationError {
    constructor(problem) {
        this.problem = problem;
    }

    createError() {
        const container = document.createElement('div');
        const title = document.createElement('h3');
        const content = document.createElement('p');
        container.classList.add('error', 'is-hide');
        title.classList.add('error-title');
        content.classList.add('error-content');
        container.append(title, content);
        this.container = container;
        this.title = title;
        this.content = content;
    }

    static destroy() {
        const errorMessage = document.querySelector('.error');
        errorMessage.classList.add('is-hide');
        setTimeout(() => errorMessage.remove(), 2000);
    }

    showMessage() {
        this.createError();
        this.title.textContent = 'An error occurred';
        this.content.textContent = `Oops, looks like something's broken. 
        The functionality of the ${this.problem} no longer works.`;
        this.container.append(this.title, this.content);
        document.body.append(this.container);
        this.container.classList.remove('is-hide');
        setTimeout(ApplicationError.destroy, 10000);
    }
}
