/* navigation menu JavaScript file
 * setting load pages
 * setting up color effects on the button
 */

import ApplicationError from './Error';
import { burger, burgerNavigation } from './burger';
import searchComponents from './components';
import getButton from './main-button';

const container = document.querySelector('.grid-container');
const header = document.querySelector('.layout-header');
const footer = document.querySelector('.layout-footer');
const navigation = document.querySelector('.navigation');
const buttons = document.querySelectorAll('.navigation-list-item');
const textLogo = document.querySelector('.text-logo');
const main = document.querySelector('.layout-main');

const textContent = {
    home: {
        h2: `Welcome to MAGICSS`,
    },
    components: {
        label: `For example: button, form or card`,
    },
    download: {
        h2: `To download components`,
        p: `<a class="components-rar" href="rar/loader.rar" download="loader.rar"> loader </a> <br>
            <a class="components-rar" href="rar/button.rar" download="button.rar"> button </a> <br>
            <a class="components-rar" href="rar/card.rar" download="card.rar"> card </a> <br>
            <a class="components-rar" href="rar/menu.rar" download="menu.rar"> menu </a> <br>
            <a class="components-rar" href="rar/table.rar" download="table.rar"> table </a> <br>
            <a class="components-rar" href="rar/form.rar" download="form.rar"> form </a> <br>
            <a class="components-rar" href="rar/slider.rar" download="slider.rar"> slider </a> <br>
            <a class="components-rar" href="rar/accordion.rar" download="accordion.rar"> accordion </a> <br>
            `,
    },
    git: {
        h2: `My GITHUB`,
        p: `The page should open in a new window. 
        If this didn't happen, your browser may have blocked it. 
        <a href="https://github.com/DenisLopatin" target="_blank"> Get to GIT </a>`,
    },
};

function click(event) {
    try {
        const { target } = event;
        const bool = target.classList.contains('navigation-list-item');
        const isLoading = navigation.getAttribute('date-loading');
        const isActive = target.getAttribute('data-active');

        if (isActive || isLoading) return;

        if (bool) {
            const data = target.getAttribute('data-navigation');
            navigation.setAttribute('date-loading', 'true');

            createPage(data, target);
        }
    } catch (err) {
        const error = new ApplicationError('get contents');
        error.showMessage();
        throw new Error(err);
    }
}

function createPage(data, target) {
    try {
        const result = preparingToUpload(target);

        if (result) return;

        const DOM_Elements = creatingElements();

        getPage(data, target, DOM_Elements).catch((err) => {
            throw new Error(err);
        });
    } catch (err) {
        const error = new ApplicationError('get contents');
        error.showMessage();
        throw new Error(err);
    }
}

async function getPage(data, target, DOM_Elements) {
    try {
        header.classList.add('is-left-translateX');
        footer.classList.add('is-right-translateX');

        for (let i = 0; i < main.children.length; i++) {
            main.children[i].classList.add('is-hide');
        }

        textLogo.classList.add('is-hide');

        setTimeout(() => {
            try {
                main.innerHTML = '';

                container.append(DOM_Elements.headerLoading);
                DOM_Elements.loader.append(DOM_Elements.loaderTime);
                main.append(DOM_Elements.loader);

                contentLoading(data, target, DOM_Elements);
            } catch (err) {
                generateError(err);
            }
        }, 250);
    } catch (err) {
        generateError(err);
    }
}

async function contentLoading(data, target, DOM_Elements) {
    try {
        [
            DOM_Elements.h2,
            DOM_Elements.p,
            DOM_Elements.label,
            DOM_Elements.input,
        ].forEach((element) => element.classList.add('is-hide'));

        DOM_Elements.h2.textContent = textContent[data].h2;
        DOM_Elements.p.innerHTML = textContent[data].p;
        DOM_Elements.label.textContent = textContent[data].label;
        DOM_Elements.label.append(DOM_Elements.input);

        const random = Math.trunc(Math.random() * 5);
        const randomTime = [10, 20, 30, 40, 50];

        setTimeout(() => {
            try {
                DOM_Elements.headerLoading.classList.remove('is-hide');
                DOM_Elements.loader.classList.remove('is-hide');

                for (let i = 1; i <= 100; i++) {
                    setTimeout(
                        () => (DOM_Elements.loaderTime.textContent = `${i}%`),
                        randomTime[random] * i,
                    );
                }
            } catch (err) {
                generateError(err);
            }
        }, 250);

        setTimeout(() => {
            downloadIsComplete(data, target, DOM_Elements);
        }, randomTime[random] * 110);
    } catch (err) {
        generateError(err);
    }
}

async function downloadIsComplete(data, target, DOM_Elements) {
    try {
        DOM_Elements.headerLoading.classList.add('is-hide');
        DOM_Elements.loader.classList.add('is-hide');

        setTimeout(() => {
            try {
                header.classList.remove('is-left-translateX');
                footer.classList.remove('is-right-translateX');
                DOM_Elements.headerLoading.remove();
                DOM_Elements.loader.remove();

                if (data === 'components') {
                    main.append(DOM_Elements.label, DOM_Elements.components);
                    const inputComponents = document.querySelector(
                        '.main-input',
                    );
                    inputComponents.addEventListener('input', searchComponents);
                } else if (data === 'git') {
                    window.open(
                        'https://github.com/DenisLopatin/MAGICSS',
                        '_blank',
                    );
                    main.append(DOM_Elements.h2, DOM_Elements.p);
                } else if (data === 'home') {
                    main.append(DOM_Elements.h2);
                    getButton();
                } else if (data === 'download') {
                    main.append(DOM_Elements.h2, DOM_Elements.p);
                }
            } catch (err) {
                generateError(err);
            }
        }, 500);

        setTimeout(() => {
            try {
                [
                    textLogo,
                    DOM_Elements.h2,
                    DOM_Elements.p,
                    DOM_Elements.label,
                    DOM_Elements.input,
                    DOM_Elements.components,
                ].forEach((element) => element.classList.remove('is-hide'));
            } catch (err) {
                generateError(err);
            }
        }, 1000);

        target.setAttribute('data-active', 'true');
        navigation.removeAttribute('date-loading');
    } catch (err) {
        generateError(err);
    }
}

function preparingToUpload(target) {
    const isDataBurger = burger.getAttribute('data-burger');

    if (isDataBurger) {
        burger.removeAttribute('data-burger');
        for (let i = 0; i < burger.children.length; i++) {
            burger.children[i].classList.remove(
                ...burger.children[i].classList,
            );
            burger.children[i].classList.add('burger-line');
        }
        burgerNavigation.classList.remove('is-width-100vw');
    }

    const bool = target.getAttribute('data-active');

    if (bool) {
        return true;
    }
    buttons.forEach((btn) => btn.removeAttribute('data-active'));
    return false;
}

function creatingElements() {
    const headerLoading = document.createElement('div');
    const h2 = document.createElement('h2');
    const p = document.createElement('p');
    const loader = document.createElement('div');
    const loaderTime = document.createElement('div');
    const label = document.createElement('label');
    const input = document.createElement('input');
    const components = document.createElement('div');

    headerLoading.classList.add('header-loading', 'is-hide');
    loader.classList.add('loader', 'is-hide');
    loaderTime.classList.add('loader-time');
    components.classList.add('components');
    h2.classList.add('main-title');
    p.classList.add('main-text');
    label.classList.add('main-label');
    input.classList.add('main-input');

    headerLoading.textContent = 'Loading';
    loaderTime.textContent = '0%';

    return {
        headerLoading,
        h2,
        p,
        loader,
        loaderTime,
        label,
        input,
        components,
    };
}

function generateError(err) {
    const error = new ApplicationError('get contents');
    error.showMessage();
    header.classList.remove('is-left-translateX');
    footer.classList.remove('is-right-translateX');
    main.innerHTML = '';
    const loading = document.querySelector('.header-loading');
    if (loading) {
        loading.remove();
    }
    const h2 = document.createElement('h2');
    const p = document.createElement('p');
    h2.classList.add('main-title');
    p.classList.add('main-text');
    h2.textContent = 'Welcome to MAGICSS';
    p.textContent = `Unfortunately, you can only observe 
                         the magic of errors right now. 
                         Something went wrong...`;
    main.append(h2, p);
    throw new Error(err);
}

navigation.addEventListener('click', click);
